import React, { Component } from 'react';
import axios from 'axios'

class Product extends Component {
    constructor(props) {
        super(props);
        
        this.state = {
            id: props.match.params.id,
            loading: true,
            data: {},
        };
    }

    formatprice(valor) {
        var formatado = "R$ " + valor.toFixed(2).replace(".",",");
        return formatado;
    

    }
    
    componentDidMount(){
        axios.all([
            axios.get(`https://api.mercadolibre.com/items/${this.state.id}`),
            axios.get(`https://api.mercadolibre.com/items/${this.state.id}/description`)
        ])
        
        .then(([item, description]) => {
            console.log(item, description)
            
            this.setState({
                data: {
                    ...item.data,
                    description: description.data.plain_text,
                },
                loading: false,
            });
            
        })
        .catch((err) => {
            console.log(err)
        });
    }
    
    render () {
        const { id, data, loading } = this.state;
        if(loading){
            return (
                <div class="loading">
                <div class="mdl-progress mdl-js-progress mdl-progress__indeterminate" id="p2"></div>       
                </div>
            );
        } else {
            return (
                
                <div className="mdl-grid">
                    <div class="loading mdl-cell mdl-cell--6-col mdl-cell mdl-cell--12-col-tablet mdl-cell mdl-cell--12-col-phone">
                        <img className="product_image" src={ data.pictures[0].url } />
                    </div>
                
                    <div class="mdl-cell mdl-cell--6-col mdl-cell mdl-cell--12-col-tablet mdl-cell mdl-cell--12-col-phone">
                        <div class="id mdl-layout-content">
                            {data.id}
                        </div>
                        <div class="quantity mdl-layout-content">
                            {data.sold_quantity} Vendidos
                        </div>
                        <div class="title mdl-layout-title">
                            {data.title}
                        </div>
                        
                        
                        <div class="price mdl-layout__title">
                            {this.formatprice(data.price)}
                        </div>
                        <div class="botao mdl-cell mdl-cell--6-col mdl-cell mdl-cell--12-col-tablet mdl-cell mdl-cell--12-col-phone">
                            <button class="mdl-button mdl-js-button mdl-button--raised mdl-button--colored">Comprar</button>
                            <button class="lista mdl-button mdl-js-button mdl-js-ripple-effect">Enviar por E-mail</button>
                        </div>
                        
                        
                    </div>
                                   
                </div>
                
                
                
                );
            }
            
        }
    }
    
    export default Product;